@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,600;0,700;0,800;0,900;1,300&display=swap');


.App {
  background-image: url("./assets/images/bg.png");
  font-family: 'Poppins', sans-serif;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.mainTitle {
  color: #F7F7F8;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 30px;
  margin: 0 auto;
  max-width: 300px;
  text-align: center;
  padding-top: 1.5rem;
}

.highlighted {
  color: #FF747E;
}

.subtitle {
  color: #F7F7F8;
  font-size: .8rem;
  font-weight: 600;
  margin: 0 auto;
  text-align: center;
  padding-top: 1rem;
}

@media screen and (max-height: 739px) {
  .mainTitle {
    font-size: 1.2rem;
    max-width: 250px;
    padding-top: .5rem;
    line-height: 1.2rem;
  }
  .subtitle {
    display: none;
  }
}