.contestants_area {
  max-height: 320px;
  overflow: hidden;
}
.girl_squares {
  display: flex;
  flex-direction: column;
  overflow-x: scroll;
  overflow-y: hidden;
  gap: 15px;
}

[class*=row] {
  display: flex;
  position: relative;
}

.row1, .row3 {
  position:relative;
  left: 90px;
}

.row2 {
  position: relative;
  left: 30px;
  top: -65px;
}

.row3 {
  position: relative;
  top: -130px;
}

@media screen and (max-height:800px){
  .contestants_area {
    max-height: 38vh;
  }
  .girl_squares {
    padding-top: 1vh;
  }
}
