.main_container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.podium_container {
  width: fit-content;
  height: fit-content;
  display: flex;
  gap: 15px;
  transform-origin: center;
  transform: rotate(45deg);
}

.col1, .col2, .col3 {
  display: flex;
  flex-direction: column;
  gap: 15px;
}


@media screen and (max-height: 780px) {
  .main_container {
    transform: scale(85%);
  }
}
