
.girlPic {
  transform: rotate(45deg);
}
.girlPic img {
  transform: rotate(-45deg);
  width: 120px;
  min-height: 120px;
  transition: .15s all;
  background-color: transparent;
  filter: drop-shadow(0 0 10px rgba(242, 242, 247, 0.11));
}

.clicked {
  transition: .15s all;
  filter: drop-shadow(7px 4px 0 #FF747E) 
          drop-shadow(-7px -4px 0 #FF747E);
}

.unavailable {
  filter: grayscale();
  pointer-events: none;
  user-select: none;
}